.error-container {
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.error-header {
    font-family: Helvetica, sans-serif;
    font-style: normal;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    margin: 20px;
    color: #1e1e1e;
}

.error-text {
    font-family: Helvetica, sans-serif;
    font-style: normal;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
    color: #1e1e1e;
}
